import "./card.css";

export const createCard = ({
  color = "black",
  background = "black",
  fontSize = "1.9rem",
  bodyTitle = "Defualt Title",
} = {}) => {
  // Create the card container
  const card = document.createElement("div");
  card.classList.add("card");

  // Create the image element
  const cardImg = document.createElement("img");
  cardImg.src =
    "https://www.veeforu.com/wp-content/uploads/2023/03/Purple-cyan-gradient-Youtube-thumbnail-background-png-1024x576.jpg";
  cardImg.classList.add("card-img");

  // Create the body text
  const cardBody = document.createElement("h1");
  cardBody.classList.add("card-body");
  cardBody.textContent = bodyTitle;
  cardBody.style.color = color;
  cardBody.style.backgroundColor = background;
  cardBody.style.fontSize = fontSize;

  // Create the rating
  const cardRate = document.createElement("p");
  cardRate.classList.add("card-rate");
  cardRate.textContent = "* * * * *";
  cardBody.style.color = color;

  // Create the secondary text
  const cardTitle = document.createElement("h4");
  cardTitle.classList.add("card-title");
  cardTitle.textContent = "Secondary Text";

  // Create the description paragraph
  const cardPara = document.createElement("p");
  cardPara.classList.add("card-para");
  cardPara.textContent = "Lorem ipsum dolor sit amet.";

  //HTML String
  const htmlString = `<div>
    <h1>Hello, World!</h1>
    <p>This is HTML as a string in JavaScript.</p>
  </div>`;

  document.body.inputMode = htmlString;

  // Append all elements to the card
  card.append(cardImg);
  card.append(cardBody);
  card.append(cardRate);
  card.append(cardTitle);
  card.append(cardPara);
  card.append(htmlString);

  return card;
};
